import React, {Component} from 'react'
import ChuckNorrisAPI from '../services/chuckNorris'
import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';
import CategoryListItem from './CategoryListItem'
import './../../style/CategoryList.css'
import PropTypes from 'prop-types'
import {bindActionCreators} from 'redux';
import {setCategories} from "../redux/actions";
import {connect} from 'react-redux';

const CreateGrid = ({categories}) => {
    return (
        <Grid container spacing={8} style={{padding: 12}}>
            {
                categories.map(value => (
                    <Grid item xs sm lg xl md key={value}>
                        <CategoryListItem category={value}/>
                    </Grid>
                ))
            }
        </Grid>
    )
}

class CategoryList extends Component {
    constructor(props) {
        super(props)
        this.state = {
            error: null,
            loading: false
        }
    }

    componentDidMount() {
        const { setCategories } = this.props
            this.setState({ loading: true })
        ChuckNorrisAPI.categories()
            .then(data => setCategories(data.responseData))
            .catch(error => this.setState({ error }))
        this.setState({ loading: false })
    }

    render() {
        const {error, loading} = this.state
        const { categories } = this.props
        if (error) {
            return (
                <p>
                    {JSON.stringify(error)}
                </p>
            )
        }
        if (loading) {
            return (
                <div>
                    <CircularProgress/>
                </div>
            )
        } else {
            const gridColumns = []
            const gridNumbers = parseInt(((categories.length) / 4).toString())
            for (let i = 0; i < gridNumbers; i++) {
                gridColumns.push(categories.slice(i * 4, (i + 1) * 4))
            }
            return (
                <div className={"Grid-div-root"}>
                    {
                        gridColumns.map(
                            gridColumn => <CreateGrid categories={gridColumn} key={gridColumn.toString()}/>
                        )
                    }
                </div>
            )
        }
    }
}

CreateGrid.propTypes = {
    categories: PropTypes.arrayOf(
        PropTypes.string.isRequired
    ).isRequired
}

const mapStateToProps = store => ({
    categories: store.categoriesState.value
})

const mapDispatchToProps = dispatch =>
    bindActionCreators({setCategories}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(CategoryList)