import React, {useState} from 'react'
import ChuckNorrisAPI from '../services/chuckNorris'
import PropTypes from 'prop-types'
import useAsync from "../hooks/useAsync";
import CircularProgress from '@material-ui/core/CircularProgress';
import LinearProgress from '@material-ui/core/LinearProgress';
import './../../style/CategoryListItem.css'
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const CategoryListItem = ({category}) => {
    const {error, data, loading} = useAsync(() => ChuckNorrisAPI.randomJokeFromCategory(category))
    const [joke, setJoke] = useState({})
    const [isLoading, setIsLoading] = useState(false)
    if (error) {
        return (
            <p>
                {JSON.stringify(error)}
            </p>
        )
    } else if (loading) {
        return (
            <div>
                <CircularProgress/>
            </div>
        )
    } else {
        const {responseData} = data
        const jokeValue = joke.value || responseData.value
        return (
            <Card className={"Card"}>
                <CardMedia
                    className={"Image"}
                    image={`https://source.unsplash.com/400x240/?${category}`}
                    title={category}
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                        {category.replace(/\b\w/g, l => l.toUpperCase())}
                    </Typography>
                    {isLoading
                        ?
                        <div>
                            <LinearProgress/>
                        </div>
                        :
                        <Typography component="p">
                            {jokeValue}
                        </Typography>
                    }
                </CardContent>
                <CardActions>
                    <Button size="small"
                            color="primary"
                            variant="outlined"
                            disabled={isLoading}
                            onClick={
                                async () => {
                                    setIsLoading(true)
                                    const response = await ChuckNorrisAPI.randomJokeFromCategory(category)
                                    setIsLoading(false)
                                    setJoke(response.responseData)
                                }
                            }>
                        Update Joke
                    </Button>
                </CardActions>
            </Card>
        )
    }
}

CategoryListItem.propTypes = {
    category: PropTypes.string.isRequired
}

export default CategoryListItem